import React, { useState, useEffect } from "react";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import Skeleton from "@mui/material/Skeleton";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Chip from "@mui/material/Chip";
import AudioPlayer from "react-h5-audio-player";
import "react-h5-audio-player/lib/styles.css";
import { DropzoneArea } from "material-ui-dropzone";
import Snackbar from "@mui/material/Snackbar";
const axios = require("axios");
const baseUrl = "";
const dateFormat = require("dateformat");

const UserSection = (props) => {
  const [user, setUser] = useState(null);

  const fetchUserData = async () => {
    const result = await axios.get(`${baseUrl}/getUser`);
    setUser(result.data);
    console.log(result.data);
  };

  const deleteAudioTrack = async () => {
    const result = await axios.get(`${baseUrl}/deleteAudioTrack`);
    setUser(null);
  };

  const uploadFile = async (file) => {
    try {
      let formData = new FormData();
      formData.append("file", file[0]);

      const result = await axios.post(`${baseUrl}/uploadAudioTrack`, formData);

      console.log(result.data);
      alert("Audio added successfully");
      setUser(null);
    } catch (error) {
      alert("Failed to upload audio");
      setUser(null);
    }
  };
  return (
    <>
      <br></br>

      <Grid align="center" justify="center">
        <Grid item xs={12} sm={12} alignContent="center">
          <Button onClick={fetchUserData} variant="contained" color="primary">
            Fetch Data
          </Button>
        </Grid>
      </Grid>
      <br></br>
      <Grid align="center" justify="center">
        <Grid item xs={10} sm={8} alignContent="center">
          {!user && (
            <Skeleton variant="rectangular" width="100%" height="50vh" />
          )}
          <br></br>
          {user && (
            <>
              <div style={{ textAlign: "start" }}>
                <div>
                  <b>First Name: </b>
                  {user.firstName}
                </div>
                <br></br>
                <div>
                  <b>Last Name: </b>
                  {user.lastName}
                </div>
                <br></br>
                <div>
                  <b>DOB: </b>
                  {dateFormat(user.dob, "ddd, mmm dS, yyyy")}
                </div>
                <br></br>
                <div>
                  <b>Interests: </b>
                  {user.interests.map((el, index) => {
                    return (
                      <Chip
                        key={index}
                        label={el}
                        size="small"
                        variant="outlined"
                        style={{ margin: "2px" }}
                      />
                    );
                  })}
                </div>
                <br></br>
                <div>
                  <b>Track name: </b>
                  {user.originalName}
                </div>
                <br></br>
                <AudioPlayer
                  src={baseUrl + "/" + user.audioTrack}
                  onPlay={(e) => console.log("onPlay")}
                  // other props here
                />
                <br></br>

                <DropzoneArea
                  showAlerts={false}
                  maxFileSize={300000000}
                  onChange={(file) => {
                    if (file && file[0]) uploadFile(file);
                  }}
                />
                <br></br>
                <Grid item xs={12} sm={12} alignContent="flex-end">
                  <Button
                    onClick={deleteAudioTrack}
                    variant="outlined"
                    color="error"
                  >
                    Delete track
                  </Button>
                </Grid>
              </div>
            </>
          )}
        </Grid>
      </Grid>

      <br></br>
      <br></br>
    </>
  );
};
export default UserSection;
